@import "../../styles/colors.scss";

.set-payment-method {
  .amount-information-block {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: url("https://enter.bonuz.market/user/assets/img/background.png") no-repeat center;
    width: 350px;
    max-width: 100%;
    height: 383px;
    background-size: contain;
    margin-bottom: -20px;
  }

  .reserve-amount-info {
    color: white;
    font-size: 16px;
  }

  .reserve-amount-title {
    color: $pinkGlow;
    font-size: 30px;
  }

  .total-amount {
    margin-top: 20px;
    color: white;
    font-size: 12px;
  }
}
