@import "../../styles/colors.scss";

.congratulations-page {
  .logo {
    margin-top: 100px;
  }

  .amount-information-block {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .reserve-amount-info {
    color: white;
    font-size: 16px;
  }

  .reserve-amount-title {
