@import "styles/colors.scss";

.App {
  text-align: center;
  height: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;

  .main-title {
    color: $pinkDark;
    font-size: 22px;
    padding: 20px 0;
  }

  .info-title {
    color: white;
    margin-bottom: 16px;
  }

  .page-content {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }

  .action-buttons {
    flex-grow: 1;
    width: calc(100% - 30px);
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;

    &__button {
      margin: 0 0 10px;
      width: 100%;
      max-width: 400px;
      height: 50px;
      color: white;
      cursor: pointer;

      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid $pinkGlow;
      background-color: transparent;

      &-dark {
        background-color: $pinkDark;
      }

      &, &:active, &:hover {
        outline: none;
        text-decoration: none;
      }
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
