@import "../../styles/colors.scss";

.input-amount-page {
  .amount-input {
    width: calc(100% - 30px);
    max-width: 400px;
    margin: 0 10px 10px;
    height: 50px;
    color: white;
    cursor: pointer;
    display: flex;
    background-color: transparent;
    outline: none;
    box-sizing: border-box;
    text-align: center;

    &, &:focus, &:active {
      border: 2px solid $pinkGlow;
    }
  }

  .reserve-amount {
    margin-top: 5px;
    color: $pinkGlow;
  }

  .total-amount {
    margin-top: 5px;
    color: white;
  }
}
