@import "../../styles/colors.scss";

.select-token {
    > .tokens {
        > .token {
            box-shadow: 0 0 0 1px black;

            &.selected {
                background-color: $pinkDark;
            }
            > img {
                margin-right: 20px;
            }
        }
    }

    > .networks .network {
        background-color: $pinkDark;
    }

    > .networks, .tokens {
        display: flex;
        flex-wrap: wrap;
        max-width: 720px;
        width: 100%;

        > .token, .network {
            width: 210px;
            margin: 0 10px 10px;
            height: 50px;
            color: white;
            cursor: pointer;

            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px solid $pinkGlow;
        }
    }
}

@media (max-width: 720px) {
    .select-token {
        > .tokens, .networks {
            .network, .token {
                width: calc(50% - 25px);
            }
        }
    }
}
