@import "../../styles/colors.scss";

.wallet-connection {
  .wallet {
    margin: 0 0 10px;
    padding: 0 20px;
    width: calc(100% - 30px);
    max-width: 400px;
    height: 50px;
    color: white;
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 2px solid $pinkGlow;
    background-color: transparent;
  }
}
